import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import { Col, Container, Row } from 'react-bootstrap'
import './VideoBanner.css'
export default class VideoBanner extends React.Component {
    render() {
        return (
            <Container fluid className="p-3 bg-dark text-light" id='video-banner-wrapper'>
                <Row>
                    <Col xs={12} sm={12} md={6}>
                        <img src='transcac.svg' alt='ff' className='' />
                    </Col>
                    <Col xs={12} sm={12} md={6}>
                        <div className='border-gradient border-gradient-green display-1 p-5 text-center fw-bold'>
                            <span className='text-gradient-green'>
                                Power
                                <br />
                                Of
                                <br />
                                Decentralized
                                <br />
                                Finance
                            </span>
                        </div>
                    </Col>
                </Row>
            </Container>
        )
    }
}