import React, { Component } from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import { Container, Row, Col, ListGroup } from 'react-bootstrap'


function FooterLink(props){
    return(
            <>       
             <ListGroup.Item className='bg-dark text-light border-bottom border-secondary'>
                <a href={props.href} target='_blank' className='text-light'>{props.linkOwner}</a>
             </ListGroup.Item>
        
        </>

    )
}
class FooterLinks extends Component{
    render(){
        return(
            <Container fluid className='bg-dark text-light p-5'>
            <Container className='bg-dark'>
                <Row>
                    <Col className='mt-2'>
                    <ListGroup variant="flush">
                        <FooterLink href='https://www.coingecko.com/api/v3' linkOwner='Go to Coingecko&trade;'/>
                        <FooterLink href='https://www.github.com/ehi_dwebdev' linkOwner='Go to Documentation.&trade;'/>
                        <FooterLink href='https://www.github.com/ehi_dwebdev' linkOwner='FAQ'/>
                        <FooterLink href='https://www.coingecko.com' linkOwner='Crypto News'/>
                    </ListGroup>
                    </Col>

                    <Col className='mt-2'>
                        <ListGroup variant="flush">
                            <ListGroup.Item className='bg-dark text-light border-bottom border-secondary'>Discord</ListGroup.Item>
                            <ListGroup.Item className='bg-dark text-light border-bottom border-secondary'>Twitter</ListGroup.Item>
                            <ListGroup.Item className='bg-dark text-light border-bottom border-secondary'>Telegram</ListGroup.Item>
                            <ListGroup.Item className='bg-dark text-light border-bottom border-secondary'>Youtube</ListGroup.Item>
                        </ListGroup>
                    </Col>

                    <Col className='mt-2'>
                        <ListGroup variant="flush">
                            <ListGroup.Item className='bg-dark text-light border-bottom border-secondary'>Seek Suport</ListGroup.Item>
                            <ListGroup.Item className='bg-dark text-light border-bottom border-secondary'>Report a bug</ListGroup.Item>
                        </ListGroup>
                    </Col>
                </Row>
            </Container>
        </Container>
        )
    }
}

class DevLink extends Component{
    render(){
        return(
            <Row>
                <Col className='text-center bg-dark text-light p-3'>
                    Developed by:
                    <br />
                   <span className='dev-link'> Ehi_DWebDev </span>
                </Col>
            </Row>
        )
    }
}
export default class Footer extends Component {
    render() {
        return (
            <div>
           <FooterLinks />

           <Col className='text-center bg-dark text-light p-3 lead'>
                    Developed by:
                    <br />
                   <span className='dev-link'>
                    <a href='https://www.github.com/ehi_dwebdev' target='_blank'>Ehi_DWebDev</a>
                   </span>
                </Col>
           </div>
        )
    }
}