import React, { Component } from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import { Container, Row, Col } from 'react-bootstrap'
import './Trade.css'
export default class Sell extends Component {
    render() {
        return (
            <Container fluid className='mt-5 bg-dark text-light p-lg-5 border-bottom border-info'>
                <Row>
                    <Col xs={12} sm={12} md={6} className='vertical-center'>
                        <span className='fs-1 border-gradient border-gradient-purple bg-dark text-light p-5 text-center'> Sell Coin &nbsp;
                            <span className='text-primary'>&rarr;</span>
                        </span>
                    </Col>
                    <Col xs={12} sm={12} md={6} className='vertical-center text-center'>
                        <a className='buy-link fs-3 text-light text-decoration-none border-gradient border-gradient border-gradient-green p-md-5' rel="noreferrer" href='https://www.binance.com/trade/pvp' target="_blank">
                            Click here to sell your coin via
                            Binance&trade; pvp.
                            <br />
                            <small className='text-secondary'>
                                Don't worry, it opens in another tab!
                            </small>
                        </a>
                    </Col>
                </Row>
            </Container>
        )
    }
}