import React from 'react'
import { Navbar, Container, Nav, NavDropdown } from 'react-bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css';
import './Header.css'
class Header extends React.Component {
  render() {
    return (
      <Navbar collapseOnSelect expand="lg" variant='light' style={{ fontSize: 'larger' }}>
        <Container>
          <Navbar.Brand href="#home"><span className='brand-logo'>NewtCoin</span></Navbar.Brand>
          <Navbar.Toggle aria-controls="responsive-navbar-nav" />
          <Navbar.Collapse id="responsive-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link href="#features">Getting Started</Nav.Link>
              <Nav.Link href="#pricing">Staking</Nav.Link>
              <NavDropdown title="Trade" id="collasible-nav-dropdown">
                <NavDropdown.Item href="#action/3.1">View Market</NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item href="#action/3.2">Buy Coin</NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item href="#action/3.3">Sell coin</NavDropdown.Item>
                <NavDropdown.Divider />
              </NavDropdown>
            </Nav>
            <Nav>
              <Nav.Link href="#deets">Dark Mode</Nav.Link>
              {/* <Nav.Link eventKey={2} href="#memes">
                Dank memes
              </Nav.Link> */}
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    )
  }
}

export default Header