import React from 'react'
import { Container, Row, Col, Card, ListGroup, ListGroupItem } from 'react-bootstrap'
import './Teams.css'
class ContactLink extends React.Component{
  render(){
    return(
      <p>
      <span className='media-icon'>{this.props.icon}</span>&nbsp;
      <span className='media-link-wrapper'>
      <a href={this.props.link} className='media-link'>
        {this.props.platform}
        </a>
      </span>
      </p>
    )
  }
}

