import React, { Component } from 'react'
import { Container, Row, Col, ListGroup, Accordion } from 'react-bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css';
import './Staking.css'

function StakeComponents() {
    return (
        <Accordion defaultActiveKey="0" flush>
            <Accordion.Item eventKey="0">
                <Accordion.Header className=''><span className='text-capitalize'>A new way to earn!</span></Accordion.Header>
                <Accordion.Body>
                    Welcome to the brand new feature geared at maximising your profits.
                    <br />
                    With the new stake pool, you get to allocate or delegate a certain amount of your coins to the coin pool  and this is used in trading 
                    alongside other coins by an automated system and the profits shared with you.Think of it like a trading bot yeah?
                </Accordion.Body>
            </Accordion.Item>
            <Accordion.Item eventKey="1">
                <Accordion.Header className=''><span className='text-capitalize'>AI insures you against loss!</span></Accordion.Header>
                <Accordion.Body>
                    So what you say?<br />
                    As a way to insure our lients against loss, th system is programmed to take tradesonly after a consideration of the probability of 
                    failure or sucess as determined by a complex AI created for the purpose. This occurs by siftifng through thousands of other previously-existing
                    records and using these data to make financial analysis and take trades upon confirmation by an admin.
                </Accordion.Body>
            </Accordion.Item>
            <Accordion.Item eventKey="2">
                <Accordion.Header className=''><span className='text-capitalize'>No minimum thresholds! </span></Accordion.Header>
                <Accordion.Body>
                   You can partake in this feature, your initial deposit notwithstanding!
                   <br />
                   Deposit as low as 1 <span className='brand-name'>NewtCoin</span> and be part of this. THe omnly drawback is that; since the AI works with only a specified
                   limit of coins per transaction, your deposit would be added to a pool of other users with similar deposits until the minimum threshold to trade with is reached.
                   <br />
                   This then implies that the profits received would be lesser as the AI would distribute based on the amount contributed to the pool.
                </Accordion.Body>
            </Accordion.Item>
            <Accordion.Item eventKey="3">
                <Accordion.Header className=''><span className='text-secondary text-capitalize'>Coming soon...</span></Accordion.Header>
                <Accordion.Body>
                 <span className='text-muted'>Coming soon in n days...</span>   
                </Accordion.Body>
            </Accordion.Item>
        </Accordion>
    )
}
export default class Staking extends Component {
    render() {
        return (
            <Container className='mt-5'>
                <Row>
                    <Col xs={12} md={12} lg={6} svg-wrapper>
                        <img src='crypto2.svg' alt=''/>
                    </Col>

                    <Col xs={12} md={12} lg={6} className='stakeContent'>
                        <Container>
                            <ListGroup as="ul">
                                <ListGroup.Item as="li" >
                                    <h1 className='display-5 stake-header'>
                                        Staking
                                    </h1>
                                </ListGroup.Item>
                                <ListGroup.Item as="li">
                                    <StakeComponents />
                                </ListGroup.Item>
                            </ListGroup>
                        </Container>
                    </Col>
                </Row>
            </Container>
        )
    }
}