import React, { Component } from 'react'
import { Container, Card, Row, Col } from 'react-bootstrap'
import './GettingStarted.css'
export default class AboutUs extends Component {
  render() {
    return (
      <Container fluid className='p-5'>

        <Row>
          <Col xs={12} md={6} className='vertical-center'>
            <Card className='card-el card-el-left'>
              <Card.Body>
                <Card.Title>
                  <h2 className='display-6'>What is Coin about?</h2>
                </Card.Title>
                <Card.Text className='lh-lg'>
                  <span className='brand-text'>NewtCoin</span> is a cryptocurrency project of the future.
                  Designed to combat and solve existing, emerging and predicted issues in the blockchain industry.
                  It has been developed to truly incorporate the essence of decentralized finance and hence; be able to resist extreme market manipulations as seen
                  in other crypto projects currently existing.
                  <br />
                  It is a cryptocurrency with features such as staking, traditional trading and a proposed web app to help keep check of your crypto portfolio
                  so as to minimize losses as seen frequently among traders.
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>

          <Col>
          <img src='question1.svg' alt='' />
          </Col>
        </Row>


        <Row className='mt-5'>
          <Col>
          <img src='vision1.svg' alt='' />
          </Col>


          <Col xs={12} md={6} className='vertical-center'>
            <Card className='card-el card-el-right'>
              <Card.Body>
                <Card.Title>
                  <h2 className='display-6'>Our Vison and Projection</h2>
                </Card.Title>
                <Card.Text className='lh-lg'>
                  We aim at being a ruling influence in the cryptocurrency space and continue to explore the endless potentials of the market and offer these services
                  in a secure and trusted environment.
                  We aim at creating a system that automatically proffers a solution to the issues plaguing the blockchain world.
                  <br />
                  It is our goal to offer you the best experience with crypto and make life easier in general with our traditional trading features targeted at 
                  being able to handle and replace fiat, in day-to-day transactions.
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>

        </Row>

      </Container>
    )
  }
}