import React, { useState, useEffect } from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import { Container, Row, Col, ListGroup } from 'react-bootstrap'
import './Trade.css'
export default class ViewMarket extends React.Component{
    constructor(){
        super();
        this.state= {
            endpoints: [],
            DataIsLoaded: false
        }

    }
   componentDidMount(){
            fetch(`https://api.coingecko.com/api/v3/coins/markets?vs_currency=ngn&ids=bitcoin`)
                .then(response => response.json())
                .then(
                    (json) => {
                        this.setState({
                            endpoints: json,
                            DataIsLoaded: true
                        })
                    }
                )

        }
render(){
    const { DataIsLoaded, endpoints} = this.state;
    if(!DataIsLoaded) return <div> <h2>Pleasewait for some time... Loading</h2></div>

    return (
        <Container fluid className='mt-5 bg-dark text-light p-lg-5 border-top border-info'>
            <Row>
                <Col xs={12} sm={12} md={6} className='vertical-center'>
                    <span className='fs-1 border-gradient border-gradient-purple bg-dark text-light p-5 text-center'> See Market &nbsp;
                        <span className='text-primary'>&rarr;</span>
                    </span>
                </Col>
                <Col xs={12} sm={12} md={6} className='vertical-center'>
                    <ListGroup variant="flush" className='border-gradient border-gradient-green p-md-5'>
                        <ListGroup.Item className='bg-dark text-light border-bottom border-secondary fs-4 fw-bold lead'>
                            <span className='vertical center coin-logo-wrapper'><img src={endpoints[0].image} alt=' ' className='coin-logo' /> </span>
                            {endpoints[0].name}
                        </ListGroup.Item>
                        <ListGroup.Item className='bg-dark text-light border-bottom border-secondary fs-4'>Current Price : &#8358; {endpoints[0].current_price} </ListGroup.Item>
                        <ListGroup.Item className='bg-dark text-light border-bottom border-secondary fs-4'>24Hr High Price : &#8358; {endpoints[0].high_24h} </ListGroup.Item>
                        <ListGroup.Item className='bg-dark text-light border-bottom border-secondary fs-4'>Percentage Change In Price(24) : {endpoints[0].price_change_percentage_24h}%</ListGroup.Item>
                    </ListGroup>
                </Col>
            </Row>
        </Container>
    )
}
}