import React, { Component } from 'react'
import { Container, Row, Col } from 'react-bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css';
import './Banner.css'
const users = {
    number: 1
}
export default class Banner extends Component {
    render() {
        return (
            <Container fluid className="mt-5 bg-primary p-5">
                <Container>
                <Row>
                    <Col xs={12} sm={6} md={6} className=''>
                       <span className='banner-text-left' style={{fontFamily:'Ubuntu'}}>
                         Over {users.number} billion coins in Circulation 
                         </span>
                    </Col>

                    <Col xs={12} sm={12} md={6} className='banner-right vertical-center'>
                        <span className='banner-text-right text-light border-gradient border-gradient-green' >
                            Join our project Today
                            </span>
                    </Col>
                </Row>
                </Container>
            </Container>
    )
    }
}