import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import { Container, Row, Col } from 'react-bootstrap'
import './Trade.css'
export default function Buy() {
        return (
            <Container fluid className='mt-5 bg-dark text-light p-lg-5'>
                <Row>
                    <Col xs={12} sm={12} md={6} className='vertical-center text-center'>
                        <a rel="noreferrer" className='buy-link fs-3 text-light text-decoration-none border-gradient border-gradient border-gradient-purple p-md-5' href='https://www.coingecko.com' target="_blank">
                            Click here to buy from Coingecko&trade;.
                            <br /> 
                            <small className='text-secondary'>
                                Don't worry, it opens in another tab!
                            </small>
                        </a>
                    </Col>
                    <Col xs={12} sm={12} md={6} className='vertical-center'>
                        <span className='fs-1 border-gradient border-gradient-green bg-dark text-light p-5 text-center'>
                        <span className='text-success'>&larr;</span>
                             &nbsp;Buy Coin &nbsp;
                        </span>
                    </Col>
                </Row>
            </Container>
        )
    }