        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320"><path fill="#d87151" fill-opacity="1" d="M0,160L24,133.3C48,107,96,53,144,32C192,11,240,21,288,37.3C336,53,384,75,432,106.7C480,139,528,181,576,176C624,171,672,117,720,80C768,43,816,21,864,53.3C912,85,960,171,1008,202.7C1056,235,1104,213,1152,176C1200,139,1248,85,1296,101.3C1344,117,1392,203,1416,245.3L1440,288L1440,320L1416,320C1392,320,1344,320,1296,320C1248,320,1200,320,1152,320C1104,320,1056,320,1008,320C960,320,912,320,864,320C816,320,768,320,720,320C672,320,624,320,576,320C528,320,480,320,432,320C384,320,336,320,288,320C240,320,192,320,144,320C96,320,48,320,24,320L0,320Z"></path></svg>
